<template>
  <div class="max-w-7xl h-auto p-[2%] bg-[#fff] mx-auto">
    <div class="min-w-[320px] max-w-lg mx-auto ">
      <p class="mt-12 mb-20 text-4xl text-center tracking-wide">Log In</p>
      <div class=" ml-4 text-lg">
        <input v-model="email" class="w-full pl-3 pt-2 pb-2 border-b-[2px] border-b-black/70 focus:outline-none" type="text" placeholder="E-mail 주소를 입력해주세요">
        <input @keydown.enter="login" v-model="password" class="w-full mt-10 pl-3 pt-2 pb-2 border-b-[2px] border-b-black/70 focus:outline-none" type="password" placeholder="비밀번호를 입력해주세요">
      </div>
      <div class="text-md mt-8 text-center">
        <button type="submit" class="w-[60%] py-2.5 text-center text-white text-xl hover:font-semibold rounded-3xl bg-blue-800/80 hover:bg-blue-800/90 focus:outline-none"
        @click="login">Sign In</button>
      </div>
      <div class="flex w-[340px] justify-between mt-8 mx-auto">
        <button class="text-sm  hover:font-semibold tracking-wider hover:underline underline-offset-2 px-4 py-1">Forgot your Email<span class="ml-[1.5px]">?</span></button>
        <button class="text-sm hover:font-semibold tracking-wider hover:underline underline-offset-2 px-4 py-1">Join In</button>
      </div>
      <div class="flex justify-around">
        <p class="cursor-pointer" @click="kakaoLoginBtn"><img :src="require(`../assets/images/login/kakao_login_medium_narrow.png`)" alt="카카오 로그인" class="w-[150px]"></p>

        <a href="https://kauth.kakao.com/oauth/authorize?client_id=c14c88b4c09d9814d010e8c73ffb006b&redirect_uri=http://localhost:8080/auth&response_type=code" class="cursor-pointer pt-[7px]" >kakaoLoginBtn2</a>
      </div>
      <p class="text-center mt-2 text-lg text-red-500 font-semibold">{{ errorMsg }}</p>
    </div>
  </div>
</template>
<script>
// eacb6b17f74c881c607784fe11e8e44b
import {auth} from '../firebase'
export default {
  name: "LoginView",
  data() {
    return {
      email: "",
      password: "",
      errorMsg: ""
    }
  },
  methods:{
    kakaoLoginBtn(){
      window.Kakao.init('0c510c2403b9188b2b2caf80016e86f8');
      // k는 대문자
      window.Kakao.Auth.login({
          success: function() {
            window.Kakao.API.request({
              url: '/v2/user/me',
              data: {
                property_keys: [
                  "kakao_account.profile",
                  "kakao_account.email"
                ]
              },
              success: function(res){
                console.log(res)
              },
              fail: function(err) {
                console.log(err)
              }
            })
          },
        fail: function(error){
          console.log(error)
        }
      })
    },
    kakaoLoginBtn2(){

    },
    login(){
      if(!this.email){
        alert("이메일을 입력해주세요");
        return false
        // return만 써도됨
      }
      if(!this.password){
        alert("비밀번호를 입력해주세요");
        return false;
      }
      auth.signInWithEmailAndPassword(this.email, this.password).then(((user)=>{
        console.log(user.user)
        // Token은 로그인하는 유저를 증명하기 위한 특정한 값 또는 정보
        sessionStorage.setItem("refreshToken", user.user.refreshToken);
        sessionStorage.setItem("displayName", user.user.displayName)
        this.$store.commit("loginToken", { refreshToken: user.user.refreshToken, uid: user.user.uid })
        // uid - 고유 아이디 값
        this.$router.replace('/')
      }),
      ((error)=>{
        // this.errorMsg = error.message
        this.errorMsg = error.message
      })
      )
    }
  }
}
</script>
