<template lang="">
  <div class="w-full mx-auto pb-10">
    <div class="max-w-[1400px] ml-10">
      <div class="mx-auto text-xl">
        <p class="text-center">글 수정</p>
      </div>
      <div class="w-[64%] border-b mx-auto text-center my-2"></div>
      <div class="max-w-7xl mx-auto h-[800px] flex flex-nowrap">
        <div class="w-[80%] mx-auto text-center relative">
          <select class="w-[170px] mb-2 py-2 absolute focus:outline-blue-300">
            <option v-for="e in optionBox" :key="e">{{ e }}</option>
          </select>
          <input v-model="name" type="text" class="border rounded-md pl-1.5 py-1.5 w-[80%] focus:outline-none border-gray-400 mt-14" placeholder="이름" >
          <input v-model="title" type="text" class="border rounded-md pl-1.5 my-4 py-1.5 w-[80%] focus:outline-none border-gray-400" placeholder="제목">
          <textarea id="textarea" v-model="content" placeholder="내용을 입력해주세요" class="border w-[80%] 
          rounded-md pl-2 py-2 h-[40%] focus:outline-none border-gray-400 textarea"></textarea>

          <div class="flex justify-between border rounded w-[80%] py-2 px-3 mx-auto">
            <input type="file" id="image" ref="fileInput" @change="NameChange" class="hidden">
            <div class="flex gap-x-5 text-sm items-center">
              <button class="border bg-gray-700 p-1 px-2 text-white" id="openFile" @click="clickTrigger">파일선택</button>
              <p>{{ fileInfo }}</p>
            </div>
            <span><input type="checkbox" v-model="fileDel"> {{FileNameSplit}}</span>
          </div>
          <!-- <div class="flex flex-nowrap w-[80%] mx-auto tracking tight mt-3">
            <p class="">이미지 : </p>
            <input id="image" type="file" class="shadow appearance-none text-gray-700 border-gray-400 leading-tight focus:outline-none focus:shadow-outline border rouded -mt-0.5 rounded-md ml-[38px] my-4 py-1.5 pl-2 w-[80%]">
          </div> -->
          <div class="flex flex-nowrap w-[80%] mx-auto">
            <p class="mr-4 mt-5 ml-1">태그 :</p>
            <input type="text" placeholder="콤마(,)를 이용하여 복수 태그 등록 가능" class="shadow appearance-none text-gray-700 border rounded-md ml-[33px] my-4 py-1 pl-2 w-[80%] focus:outline-none border-gray-400">
          </div>
          <div class="flex w-[80%] mx-auto">
            <p class="mr-4 mt-5 ml-1 shrink-0">관련 링크 :</p>
            <input  type="text" placeholder="https://.." class="shadow appearance-none text-gray-700 border border-gray-400 rounded-md  my-4 py-1 pl-2 w-[80%] focus:outline-none ">
          </div>
          <!-- <div class="flex w-[80%] mx-auto -mt-5">
            <p class="mr-4 ml-1 invisible">관련 링크 :</p>
            <input type="text" placeholder="https://.." class="shadow appearance-none text-gray-700 border border-gray-400 rounded-md  my-4 py-1 pl-2 w-[80%] focus:outline-none">
          </div> -->
          <!-- 버튼 -->
          <div class="flex w-[80%] mx-auto mt-4 pl-[90px]">
            <button class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm basis-[40%]" @click="cancel">취소</button>
            <button class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm basis-[40%] ml-4" @click="Modify()">완료</button>
          </div>
          <!-- 버튼 -->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {db, storage} from '../../firebase'
import optionBox from '../../assets/OptionBox.json'
export default {
  name: "NoticeModify",
  data() {
    return {
      fileDel: false,
      fileInfo: '선택된 파일 없음',
      optionBox: optionBox.optionBox,
      BoardContent : [],
      name: this.$store.state.displayName,
      title: "",
      file: "",
      content: "",
      date: new Date()
    }
  },
  mounted(){
    if(this.$store.state.noticeId == 0){
      this.$router.replace("/cs/notice")
    }
    db.collection("notice").doc(this.$store.state.noticeId).get().then((data)=>{

      this.BoardContent = data.data()
      // 원래 있던 데이터를 가져와서 수정
      this.name = this.BoardContent.name
      this.title = this.BoardContent.title
      this.content = this.BoardContent.content
      if(this.file){
        this.file = this.BoardContent.file
      }
    })
  },
  methods:{
    // e는 매개변수 
    NameChange(e){
      if(e.target.files[0]){
        // 파일 선택 버튼 클릭시 change 이벤트
        this.fileInfo = e.target.files[0].name;
      }else{
        this.fileInfo = '선택된 파일 없음'
      }
    },
    clickTrigger(){
      // 클릭 이벤트로 다른 요소에 접근 시 ref값을 지정한 곳으로 강제 실행
      this.$refs.fileInput.click()
    },
    Modify(){
      if(this.fileDel){
        // delete 메소드가 따로 있음
        storage.ref().child(`images/${this.FileNameSplit}`).delete()
        this.file = ""
      }
      db.collection("notice").doc(this.$store.state.noticeId).update({
        // update함수는 수정할 수 있는 기능
        name: this.name,
        title: this.title,
        content: this.content,
        date: this.date,
        file:this.file,
        hit : 0,
        good : 0,
        bad : 0,
        uid: this.$store.state.uid,
        date: this.date
      }).then(()=>{
        alert("수정이 완료 되었습니다.")
        this.$router.replace("/cs/notice/read")
      })
    },
    cancel(){
      window.history.go(-1);
    }
  },
  computed:{
    FileNameSplit(){
      const parts = this.file.split("%2F");
      let fileName = "";
      if(this.file){
        fileName = parts[parts.length - 1].split('?')[0]
      }
      return fileName
      /*
      split - 문자열을 특정 구분자로 분리하여 배열로 반환 - 원래 문자열을 변경하지 않고 새 배열로 반환

      split(separator) - 이 형태에서 separator는 문자열을 분리할 위치를 지정하는 문자열이나 정규식 / 만약 separator이 없으면 전체 문자열이 하나의 요소로 이루어진 배열로 반환

      split(separator, limit) - limit 는 반환할 최대 요소 수를 지정 limit개의 요소를 가진 배열로 반환하면
      원래 문자열의 나머지 부분은 무시됨

      let string = "1, 2, 3, 4, 5";
      let arr = str.split("," 3);
      arr는 ["1", "2", "3"]만 출력되며 ["4", "5"]는 무시된다.
      위 예제기준 split("%2F")를 사용하여 %2F를 기준으로 분할하여 배열로 저장
      ["파이어베이스에서 나오는 이미지 주소"] 가 저장
      변수명 fileName을 통해 이번에는 ? 기준으로 새 배열을 저장
      [""]
      fileName[0] 첫번째 배열 값을 반환
      */
    }
  }
}
</script>
