<template>
  <div class="w-full pb-10">
    <div class="max-w-7xl ">
      <p class="mb-2 text-xl pl-1.5">{{ BoardContent.title }}</p>
      <div class="flex justify-between w-full p-2 border-t border-t-white bg-slate-800/90 text-white">
       <p class="">작성자 : {{ BoardContent.name }}</p>
       <div class="flex justify-end gap-x-5">
          <p @click="PageList()" 
            class="cursor-pointer hover:bg-slate-800 hover:text-white duration-300 transition-all rounded-lg"><router-link to="/cs/qna"><font-awesome-icon icon="bars" class="w-3 mx-1"/>목록</router-link></p>
          <p class=" ">{{ BoardDate }}</p>
          <!-- BoardDate(dataList.length - (index+1)) -->
        </div>
      </div>
      <div class="flex justify-end my-2">
        <!-- <p @click="PageList()" 
        class="py-1 px-1 mr-3 md:text-[16px] lg:text-lg cursor-pointer hover:bg-slate-800 hover:text-white duration-300 transition-all rounded-lg"><router-link to="/cs/notice"><font-awesome-icon icon="bars" class="w-3 mx-1"/>목록</router-link></p> -->
        <!-- <p class="bg-gray-600 ml-3 mr-2 py-1 pl-2 pr-3 text-white hover:bg-black/90"><font-awesome-icon icon="pencil" class="w-[13px] px-1"/>쓰기</p> -->
      </div>
      <div class="w-full rounded-lg md:text-[16px] lg:text-lg">
        <p v-html="BoardContent.content" class="p-3 textarea"></p>
      </div>
      <div class="flex justify-end mt-4">
        <!-- <font-awesome-icon icon="computer" /> -->
        <p @click="PageList()" 
        class="py-1 px-1 mr-3 md:text-[16px] lg:text-lg cursor-pointer hover:bg-slate-800 hover:text-white duration-300 transition-all rounded-lg"><router-link to="/cs/qna"><font-awesome-icon icon="bars" class="w-3 mx-1"/>목록</router-link></p>
        <p @click="goback()" 
        class="py-1 px-1 mr-1 md:text-[16px] lg:text-lg cursor-pointer hover:bg-slate-800 hover:text-white duration-300 transition-all rounded-lg">뒤로가기</p>
      </div>
    </div>
  </div>
  <div class="flex justify-end w-full pb-24 gap-x-5 mt-5">
    <div class="mt-[5px]">
      <router-link to="/cs/qna/list" class="bg-blue-400 hover:bg-blue-600 focus:ring-blue-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">목록</router-link>
    </div>
    <div class="gap-x-5 flex">
      <router-link to="/cs/qna/modify" class="bg-green-400 hover:bg-green-600 focus:ring-green-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">수정</router-link>

      <button class="bg-red-400 hover:bg-red-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm"
      @click="Delete">삭제</button>
    </div>
  </div>
  <div class="w-full rounded-lg md:text-[16px] lg:text-lg">
    <div class="flex justify-start ml-10 -mt-10 ">
      <input v-html="BoardContent.comment" type="text" placeholder="의견을 남겨보세요" class="shadow appearance-none text-gray-700 border border-gray-400 rounded-md my-3 py-1 pl-2 w-[60%] h-9 focus:outline-none ">
      <button @click="Comment" class="ml-3 border border-gray-400 rounded shadow appearance-none hover:bg-gray-100 transition-all duration-300 px-2 h-[39px] mt-[11px]">댓글</button>
    </div>
  </div>
</template>
<script>
import {db} from '../../firebase'

export default {
  name:"QnaRead",
  data() {
    return {
      BoardContent : [],
      BoardDate : null
    }
  },
  mounted(){
    // 리스트 페이지의 경우 모든 게시글의 날짜를 불러와야하므로 메소드에서 코드가 실행되야했지만
    // 특정 게시글은 하나의 데이터만 필요하므로 마운트에서 코드를 실행해줌.
    if(this.$store.state.qnaId == 0){
      // id 값이 0이면 에러 발생 시킨 후 튕겨내기
      // 0은 데이터가 없을 때
      this.$router.replace("/cs/qna")
    }
    db.collection("qna").doc(this.$store.state.qnaId).get().then((data)=>{
      // doc - document, 문서
      this.BoardContent = data.data()
         const date = this.BoardContent.date.seconds * 1000 + this.BoardContent.date.nanoseconds / 1000000
        const new_date = new Date(date);
        const formattedDate = (''+new_date.getFullYear()).slice(-2) +'-'
        + ('0'+ (new_date.getMonth() + 1)).slice(-2)+'-'
        + ('0'+ new_date.getDate()).slice(-2)+''
        this.BoardDate = formattedDate;
    })
  },
  methods:{
    PageList(){
      this.$router.replace("/cs/qna")
    },
    goback(){
      window.history.go(-1);
    },
    Delete(){
      let msg = confirm("삭제된 글은 복구할 수 없습니다. \r\r 삭제하시겠습니까?")
      // confirm 동작 수행 시
      if(msg){
        console.log("!!")
        db.collection("qna").doc(this.$store.state.qnaId).delete().then(()=>{
          alert("삭제 되었습니다.");
          this.$router.replace("/cs/qna")
        })
      }
    },
    Comment(){
      db.collection("qna").add({
        // update함수는 수정할 수 있는 기능
        "name": this.name,
        "title": this.title,
        "content": this.content,
        "date": this.date,
        "hit" : 1,
        "good" : 0,
        "bad" : 0,
        "uid": this.$store.state.uid,
        "file": url
      }).then(()=>{
        this.$router.replace("/cs/qna/read")
      })
    }
  }
  
}

</script>
