<template>
  <div class="w-full pb-10">
    <div class="max-w-7xl ">
      <p class="mb-2 text-xl pl-1.5">{{ BoardContent.title }}</p>
      <div class="flex justify-between w-full p-2 border-t border-t-white bg-slate-800/90 text-white">
       <p class="">작성자 : {{ BoardContent.name }}</p>
       <div class="flex justify-end gap-x-5">
          <p @click="PageList()" 
            class="cursor-pointer hover:bg-slate-800 hover:text-white duration-300 transition-all rounded-lg"><router-link to="/cs/gallery"><font-awesome-icon icon="bars" class="w-3 mx-1"/>목록</router-link></p>
          <p class=" ">{{ BoardDate }}</p>
        </div>
      </div>
      
      <div class="w-full rounded-lg md:text-[16px] lg:text-lg">
        <p v-html="BoardContent.content" class="p-3 textarea"></p>
      </div>
      <div class="flex justify-end mt-4">
        <p @click="PageList()" 
        class="py-1 px-1 mr-3 md:text-[16px] lg:text-lg cursor-pointer hover:bg-slate-800 hover:text-white duration-300 transition-all rounded-lg"><router-link to="/cs/gallery"><font-awesome-icon icon="bars" class="w-3 mx-1"/>목록</router-link></p>
        <p @click="goback()" 
        class="py-1 px-1 mr-1 md:text-[16px] lg:text-lg cursor-pointer hover:bg-slate-800 hover:text-white duration-300 transition-all rounded-lg">뒤로가기</p>
      </div>
    </div>
  </div>
  <div class="flex justify-end w-full pb-24 gap-x-5 mt-5 ">
    <div class="mt-[5px]">
      <router-link to="/cs/gallery/list" class="bg-blue-400 hover:bg-blue-600 focus:ring-blue-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">목록</router-link>
    </div>
    <div class="gap-x-5 flex">
      <router-link to="/cs/gallery/modify" class="bg-green-400 hover:bg-green-600 focus:ring-green-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">수정</router-link>

      <button class="bg-red-400 hover:bg-red-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm"
      @click="Delete">삭제</button>
    </div>
  </div>
  <!-- {{ BoardDate }} -->
</template>
<script>
import {db} from '../../firebase'

export default {
  name:"GalleryRead",
  data() {
    return {
      BoardContent : [],
      BoardDate : null
    }
  },
  mounted(){
    if(this.$store.state.galleryId == 0){
      this.$router.replace("/cs/gallery")
    }
    db.collection("gallery").doc(this.$store.state.galleryId).get().then((data)=>{
      this.BoardContent = data.data()
        const date = this.BoardContent.date.seconds * 1000 + this.BoardContent.date.nanoseconds / 1000000
        const new_date = new Date(date);
        const formattedDate = (''+new_date.getFullYear()).slice(-2) +'-'
        + ('0'+ (new_date.getMonth() + 1)).slice(-2)+'-'
        + ('0'+ new_date.getDate()).slice(-2)+''
        this.BoardDate = formattedDate;
    })
  },
  methods:{
    PageList(){
      this.$router.replace("/cs/gallery")
    },
    goback(){
      window.history.go(-1);
    },
    Delete(){
      let msg = confirm("삭제된 데이터는 복구할 수 없습니다. \r\r 삭제하시겠습니까?")
      if(msg){
        console.log("!!")
        db.collection("gallery").doc(this.$store.state.galleryId).delete().then(()=>{
          alert("삭제가 완료 되었습니다.");
          this.$router.replace("/cs/gallery")
        })
      }
    },
    
  }
  
}

</script>
<style >
  .textarea{white-space: pre;}
</style>