<template>
  <div>
    {{ $store.state.displayName }}
    {{ $store.state.loginToken }}
    {{ $store.state.kakaoArray }}
  </div>
</template>
<!-- c14c88b4c09d9814d010e8c73ffb006b -->
<script>
import axios from 'axios'
import { faRefresh } from '@fortawesome/free-solid-svg-icons'
// XkBHNanYKkJpyChoY0PvoHyIn1MU0slr - 보안코드
export default {
  name: "LoginAuth",
  data() {
    return {
      code: this.$route.query.code,
      client_secret: 'XkBHNanYKkJpyChoY0PvoHyIn1MU0slr',
      client_id: 'c14c88b4c09d9814d010e8c73ffb006b',
      grant_type: 'authorization_code',
      redirect_uri: 'http://localhost:8080/auth'
    }
  },
  mounted(){
    axios.get("https://kauth.kakao.com/oauth/token",{
      params:{
        code:this.code,
        grant_type: this.grant_type,
        redirect_uri: this.redirect_uri,
        client_id: this.client_id,
        client_secret: this.client_secret
        // kapi(kakao)는 CORS가 닫혀있어서 닉네임과 프로필 등 일부 정보는 가져올 수 있지만 액세스 토큰으로 모든 정보를 가져오는 것은 백엔드의 영역 
      }
    }).then((res)=>{
      console.log(res)

      this.$store.commit("kakaoLogin", {res})
      sessionStorage.setItem("refreshToken", res.data.access_token)
      this.$router.replace("/");
      // if(res.data.access_token)
      // 파미리터 값을 가져오면 세션 스토리지에 refreshToken 저장
      // 액세스 토큰 / 리프레시 토큰 둘중 아무 값이나 써도 상관 없음
      // 위의 두 토큰은 로그인한 계정(사용자)이 가지는 고유한 인증 키값
        window.Kakao.init('0c510c2403b9188b2b2caf80016e86f8');
        window.Kakao.API.request({
          url: '/v2/user/me',
          data: {
              property_keys: 
                [
                  "kakao_account.profile",
                  "kakao_account.email"
                ]
            },
            success: ((res)=>{
              console.log(res)
              sessionStorage.setItem("displayName", res.kakao_account.profile.nickname);
              sessionStorage.setItem("uid", res.id);
              this.$store.commit("loginState", {displayName:
              sessionStorage.getItem("displayName"), uid:
              sessionStorage.getItem("uid")})

            }),
            fail:((error)=>{
              console.log(error)
            })
          })
      })
  }
}
</script>
