<template>
  <div class="basis-full">
    <div class="max-w-7xl mx-auto my-10 border-b-2 border-gray-500">
      <ul class="flex justify-between border-t-gray-500/80 border-t-2 p-4 py-2 bg-indigo-20 text-xs sm:text-sm text-center">
        <li class="basis-20">번호</li>
        <li class="basis-6/12">제목</li>
        <li class="basis-16">글쓴이</li>
        <li class="basis-24">날짜</li>
      </ul>
      <ul v-for="(e, index) in dataList.slice()" :key="index" class="flex justify-between border-b text-center px-4 py-2 even:bg-gray-50 text-xs sm:text-sm">
        <li class="basis-20 pr-1">{{ dataList.length - index }}</li>
        <li class="basis-6/12 pl-6"><router-link to="/cs/online/read" 
          @click="$store.commit('OnlineRead', dataId[index])">
          {{ e.title }}
        </router-link></li>
        <li class="basis-24 pl-1">{{ e.name }}</li>
        <li class="basis-24 -mr-0.5 shrink-0">{{ BoardDate(dataList.length - (index+1)) }}</li>
      </ul>
    </div>
    <div class="flex justify-end pb-24">
      <router-link to="/cs/online/write" class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">글쓰기</router-link>
    </div>
  </div>
</template>
<script>
import {db} from '../../firebase'
export default {
  name:"OnlineList",
  data() {
    return {
      dataList: [],
      dataId: []
    }
  },
  methods:{
    BoardDate(index){
      const date = this.dataList[index].date.seconds * 1000 + this.dataList[index].date.nanoseconds / 1000000
      // 밀리세컨드 초를 date 객체로 변환
      const new_date = new Date(date);
      const formattedDate = (''+new_date.getFullYear()).slice(-2) +'-'
      + ('0'+ (new_date.getMonth() + 1)).slice(-2)+'-'
      + ('0'+ new_date.getDate()).slice(-2)+''
      return formattedDate
    }
  },
  mounted(){
    db.collection("online").orderBy("date", "desc").limit(10).get().then((data)=>{
      data.forEach((e)=>{
        this.dataId.push(e.id)
        this.dataList.push(e.data())
        console.log(this.dataList)
      })
    })
  }
}
</script>
