  </div> -->
  <div class="w-full bg-[#fff]">
    <div class="mx-auto h-full max-w-[1200px] box-border border rounded-md ">
      <div class="bg-white h-full w-[60%] mx-auto text-center rounded-xl mb-10 pb-5">
        <p class="text-center font-semibold text-2xl mt-10 pt-5">회원 가입</p>
        <div class="mt-10 mx-auto h-full w-[80%] border-[2px] border-black/60 text-center rounded-lg mb-5">
          <div class="w-full text-left ml-2 mb-4 mt-5 pl-2">
            <p class="mt-1 mb-2 inline"><span class="text-red-600">*</span> E-mail</p>
            <p class="inline text-[11px] font-mono text-red-500 ml-1">*표시는 필수 기입 정보입니다.</p>
            <p class="py-2 pl-2  border rounded-lg w-[70%]"><input v-model="email" type="text" class="w-[98%] py-1 pr-1 text-right" placeholder="abcd@gmail.com"></p>
          </div>
          <div class="w-full text-left ml-2 mb-4 mt-5 pl-2">
            <p class="mt-1 mb-2 inline"><span class="text-red-600">*</span> 비밀번호</p>
            <p class="inline text-[11px] font-mono text-red-500 ml-1">*표시는 필수 기입 정보입니다.</p>
            <p class="py-2 pl-2 border rounded-lg w-[70%]"><input v-model="password" type="text" class="w-[98%] py-1 pl-1 text-right" placeholder="password"></p>
          </div>
          <div class="w-full text-left ml-2 mb-4 mt-5 pl-2">
            <p class="mt-1 mb-2 inline"><span class="text-red-600">*</span> 이름</p>
            <p class="inline text-[11px] font-mono text-red-500 ml-1">*표시는 필수 기입 정보입니다.</p>
            <p class="py-2 pl-2 border rounded-lg w-[70%]"><input v-model="nickname" type="text" class="w-[98%] py-1 pl-1 text-right" placeholder="김노인"></p>
          </div>
          <div class="w-full text-left ml-2 mb-4 mt-5 pl-2">
            <p class="mt-1 mb-2 inline"><span class="text-red-600">*</span> 닉네임</p>
            <p class="inline text-[11px] font-mono text-red-500 ml-1">*표시는 필수 기입 정보입니다.</p>
            <p class="py-2 pl-2 border rounded-lg w-[70%]"><input v-model="nickname" type="text" class="w-[98%] py-1 pl-1 text-right" placeholder="닉네임"></p>
          </div>
          <div class="w-full text-left ml-2 mb-4 mt-5 pl-2">
            <p class="mt-1 ml-2"> 성별</p>
            <select class="py-2 pl-2 border rounded-lg w-[70%]">
              <option type="text" class="w-[98%] py-1 pl-1">남성</option>
              <option type="text" class="w-[98%] py-1 pl-1">여성</option>
            </select>
          </div>
          <div class="w-full text-left ml-2 mb-4 mt-5 pl-2">
            <p class="mt-1 mb-2 ml-2">연락처</p>
            <p class="py-2 pl-2 border rounded-lg w-[70%]"><input type="text" value="" placeholder="010-xxx-xxxx" class="w-[98%] py-1 pl-1 text-right"></p>
          </div>
          <div class="w-full h-16 flex flex-nowrap justify-center mb-5 mt-2">
            <button class="mr-8 ">
              <p class="font-bold text-lg border hover:bg-blue-700/30 hover:text-white rounded-lg tracking-wider px-16 py-1.5" type="submit" @click="signUp">가입하기</p>
            </button>
            <button>
              <p @click="goback" class="font-bold text-lg border hover:bg-blue-700/30 hover:text-white rounded-lg tracking-wider px-16 py-1.5">취소</p>
            </button>
          </div>
        </div>
      </div>  
    </div>
  </div>
</template>
<script>
import {auth} from '../firebase'
export default {
  name: "MemberView",
  data() {
    return {
      email:"",
      password:"",
      nickname:"",
      name:"",
      gender: ""

    }
  },
  methods:{
    signUp(){
      auth.createUserWithEmailAndPassword(this.email, this.password).then ((result)=>{
        // 비밀번호는 반드시 6자리 이상 쓰는게 규칙!
        result.user.updateProfile({displayName: this.nickname})
        // updateProfile은 구글에서 제공하는 기능이며 작명이 아님
        // console.log(result)
        // console.log(result.user)
      })
    },
    goback(){
      window.history.go(-1);
    }
  }
}
</script>
