import VueNumber from 'vue-number-animation'

library.add(faChevronDown, faBuilding, faHistory, faPaperPlane, faMedal, faLock, faUser, faArrowRightFromBracket, faCircleArrowUp, faDesktop, faComment, faTaxi, faMobile, faBars, faPencil, faComputer, faArrowRight, faThumbsUp, faThumbsDown, faHeart, faShareAlt, faBan)

