<template>
  <div class="py-24 pt-48 text-center basis-full">
    <h3 calss="text-3xl font-bold mb-5 relative after:absoulte after:w-5 after:h-0.5 after:bg-slate-800 after:-bottom-2 after:left-[50%] after:-translate-2/4">{{ title[0] }}</h3>
    <p class="text-gray-400">{{ desc[0] }}</p>
    <div v-for="e in dataList" :key="e">
      <p>{{ e.name }}</p>
      <p>{{ e.price }}</p>
    </div>
    {{ $store.state.number }}
    <button @click="$store.commit('NumberPlus')" class="border border-black/50 rounded-lg">버튼</button><br>
    {{ $store.state.Minus }}
    <button @click="$store.commit('NumberMinus')" class="border border-black/50 rounded-lg">버튼</button>
    <p>{{ $store.state.text }}</p>
  </div>
</template>
<script>
import {db} from '../../firebase'
export default {
  name: "ProductView-1",
  props: {
    title: Array,
    desc: Array
  },
  data() {
    return {
      dataList: []
    }
  },
  mounted(){
    // db.콜렉션("DB이름").get().then((데이터)=>{반복문 실행})
    db.collection("product").get().then((data)=>{
      data.forEach((e)=>{
        // forEach - 스크립트에서 돌리는 '반복문'
        // html상에서(화면) 특정 값을 뿌릴 땐 반복문으로
        console.log(e.data())
        this.dataList.push(e.data())
      })
    })
    
  }
}
</script>
