import { createStore } from 'vuex'
// vuex는 전체 데이터에서 자주 바뀌는 것을 관리하기 위한 라이브러리

export default createStore({
  state() { // 실제론 data이지만 vuex에선 state라고 표현함
    // 변수 작명할 때 겹치지 않게 조심할 것!!
    return{
      number: 0,
      Minus: 10000,
      loginChk: false,
      displayName: "",
      loginToken: null,
      uid: "",
      noticeId: 0,
      onlineId: 0,
      qnaId: 0,
      galleryId:0,
      productId:0,
      kakaoArray: []
    }
  },
  mutations: { // ajax 데이터는 못 가져옴
    // store에서는 데이터를 가져올 때 this대신 state를 씀
    // 로그인해서 페이지가 넘어가면 넘어간 데이터를 사이트 전체에서 공유하기 위해
    NoticeRead(state, payload){
      // NoticeRead playload에서 받아온 데이터를 state.noticeId에 집어넣기
      state.noticeId = payload
    },
    OnlineRead(state, payload){
      state.onlineId = payload
    },
    QnaRead(state, payload){
      state.qnaId = payload
    },
    GalleryRead(state, payload){
      state.galleryId = payload
    },
    NumberPlus(state){
      state.number++
    },
    NumberMinus(state){
      state.Minus--
    },
    loginToken(state, payload){
      state.loginToken = payload.refreshToken
      state.loginChk = true
      state.uid = payload.uid
      console.log(payload.uid)
    },
    logOutToken(state){
      state.loginChk = false
      state.loginToken = null
      state.displayName = ''
      state.uid = ''
      sessionStorage.removeItem("refreshToken")
      sessionStorage.removeItem("displayName")
    },
    loginState(state, payload){
      // localstorage - 로컬에서 저장 시 안지우면 로그가 남아있음
      // sessionstorage - 브라우저를 닫으면 로그가 날아감
      state.loginToken = sessionStorage.getItem("refreshToken");
      state.displayName = payload.displayName
      state.uid = payload.uid
      state.loginChk = true
    },
    kakaoLogin(state, payload){
      state.kakaoArray = payload,
      state.loginToken = payload.res.data.access_token
      state.loginChk = true
    }
  },
  actions: {
  },
  
})
