import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginView from '../views/LoginView.vue'
import MemberView from '../views/MemberView.vue'
import MemberInfo from '../views/MemberInfo.vue'
// 스크립트에서는 하이픈이 안먹히므로 언더바로 표시

import CompanyVue from '../views/CompanyView.vue'
import GreetingsVue from '../views/Company/GreetingsView.vue'
import HistoryVue from '../views/Company/HistoryView.vue'
import InteriorVue from '../views/Company/InteriorView.vue'
import DirectionsVue from '../views/Company/DirectionsView.vue'

import BusinessVue from '../views/BusinessView.vue'  
import BusinessVue_1 from '../views/Business/BusinessView_1.vue'  
import BusinessVue_2 from '../views/Business/BusinessView_2.vue'  
import BusinessVue_3 from '../views/Business/BusinessView_3.vue'  

import ProductVue from '../views/ProductView.vue'
import ProductVue_1 from '../views/Product/ProductView_1.vue'
import ProductVue_2 from '../views/Product/ProductView_2.vue'
import ProductVue_3 from '../views/Product/ProductView_3.vue'


import CsVue from '@/views/CsView.vue'
import NoticeVue from '@/views/Cs/NoticeView.vue'
import NoticeList from '@/views/Cs/NoticeList.vue'
import NoticeRead from '@/views/Cs/NoticeRead.vue'
import NoticeWirte from '@/views/Cs/NoticeWrite.vue'
import NoticeModify from '@/views/Cs/NoticeModify.vue'

import OnlineVue from '@/views/Cs/OnlineView.vue'
import OnlineList from '@/views/Cs/OnlineList.vue'
import OnlineRead from '@/views/Cs/OnlineRead.vue'
import OnlineWrite from '@/views/Cs/OnlineWrite.vue'
import OnlineModify from '@/views/Cs/OnlineModify.vue'

import QnaVue from '@/views/Cs/QnaView.vue'
import QnaList from '@/views/Cs/QnaList.vue'
import QnaRead from '@/views/Cs/QnaRead.vue'
import QnaWrite from '@/views/Cs/QnaWrite.vue'
import QnaModify from '@/views/Cs/QnaModify.vue'

import GalleryVue from '@/views/Cs/GalleryView.vue'
import GalleryList from '@/views/Cs/GalleryList.vue'
import GalleryRead from '@/views/Cs/GalleryRead.vue'
import GalleryWrite from '@/views/Cs/GalleryWrite.vue'
import GalleryModify from '@/views/Cs/GalleryModify.vue'

import LoginAuth from '@/views/Auth.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path:'/auth',
    name: 'LoginAuth',
    component: LoginAuth
  },
  {
    path: '/Login',
    name: 'LoginView',
    component: LoginView
  },
  {
    path: '/member',
    name: 'MemberView',
    component: MemberView
  },
  {
    path: '/memberinfo',
    name: 'MemberInfoView',
    component: MemberInfo
  },
  {
    path: '/Company',
    name: 'CompanyView',
    component: CompanyVue,
    redirect: "/company/greetings", // redirect - 회사소개는 아무것도 없으므로 해당 페이지로 강제 연결
    children: [  // children은 해당 페이지에 중복 라우터를 걸기 위한 문법
      {
        path: '/company/greetings',
        name: 'GreetingsView',
        component: GreetingsVue
      },
      {
        path: '/company/history',
        name: 'HistoryView',
        component: HistoryVue
      },
      {
        path: '/company/interior',
        name: 'InteriorView',
        component: InteriorVue
      },
      {
        path: '/company/directions',
        name: 'DirectionsView',
        component: DirectionsVue
      }
    ]
  },
  {
    path: '/business',
    name: 'BusinessVue',
    component: BusinessVue
  },
  {
    path: '/business',
    name: 'Business',
    component: BusinessVue,
    redirect: '/business/business_1',
    children: [  
      {
        path: '/business/business_1',
        name: 'BusinessView_1',
        component: BusinessVue_1
      },
      {
        path: '/business/business_2',
        name: 'BusinessView_2',
        component: BusinessVue_2
      },
      {
        path: '/business/business_3',
        name: 'BusinessView_3',
        component: BusinessVue_3
      }
    ]
  },
  {
    path: '/product',
    name: 'Product',
    component: ProductVue,
    redirect: '/product/product_1',
    children: [ 
      {
        path: '/product/product_1',
        name: 'ProductView_1',
        component: ProductVue_1
      },
      {
        path: '/product/product_2',
        name: 'ProductView_2',
        component: ProductVue_2
      },
      {
        path: '/product/product_3',
        name: 'ProductView_3',
        component: ProductVue_3
      },
    ]
  },
  {
    path: '/cs',
    name: 'CsView',
    component: CsVue,
    redirect: '/cs/notice',
    children: [ 
      {
        path: '/cs/notice',
        name: 'NoticeView',
        component: NoticeVue,
        redirect: "/cs/notice/list",
        children: [
          {
            path: "/cs/notice/list",
            name: "NoticeList",
            component: NoticeList
          },
          {
          path: "/cs/notice/write",
          name: "NoticeWrite",
          component: NoticeWirte
          },
          {
            path: "/cs/notice/modify",
            name: "NoticeModify",
            component: NoticeModify
          },
          {
            path: "/cs/notice/read",
            name: "NoticeRead",
            component: NoticeRead
          }
        ]
      },
      {
        path: '/cs/online',
        name: 'OnlineView',
        component: OnlineVue,
        redirect: "/cs/Online/list",
        children: [
          {
            path: "/cs/online/list",
            name: "OnlineList",
            component: OnlineList
          },
          {
          path: "/cs/online/write",
          name: "OnlineWrite",
          component: OnlineWrite
          },
          {
            path: "/cs/online/modify",
            name: "OnlineModify",
            component: OnlineModify
          },
          {
            path: "/cs/online/read",
            name: "OnlineRead",
            component: OnlineRead
          }
        ]
      },
      {
        path: '/cs/qna',
        name: 'QnaView',
        component: QnaVue,
        redirect: "/cs/qna/list",
        children: [
          {
            path: "/cs/qna/list",
            name: "QnaList",
            component: QnaList
          },
          {
          path: "/cs/qna/write",
          name: "QnaWrite",
          component: QnaWrite
          },
          {
            path: "/cs/qna/modify",
            name: "QnaModify",
            component: QnaModify
          },
          {
            path: "/cs/qna/read",
            name: "QnaRead",
            component: QnaRead
          }
        ]
      },
      {
        path: '/cs/gallery',
        name: 'GalleryView',
        component: GalleryVue,
        redirect: "/cs/gallery/list",
        children: [
          {
            path: "/cs/gallery/list",
            name: "GalleryList",
            component: GalleryList
          },
          {
          path: "/cs/gallery/write",
          name: "GalleryWrite",
          component: GalleryWrite
          },
          {
            path: "/cs/gallery/modify",
            name: "GalleryModify",
            component: GalleryModify
          },
          {
            path: "/cs/gallery/read",
            name: "GalleryRead",
            component: GalleryRead
          }
        ]
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
